<template>
  <div>
    <template v-for="exhibition in source" v-bind:key="exhibition.topic">
      <v-btn
          variant="outlined"
          @click="loadData(exhibition.topic)"
      >
        {{ exhibition.name }}
      </v-btn>
    </template>
  </div>
  <v-card v-if="jsonData" variant="tonal">
    <v-card-title>
      {{ information.fullname }}
      <a :href="information.external_link" target="_blank">
        <v-icon density="default" icon="mdi-open-in-new"></v-icon>
      </a>
    </v-card-title>
    <v-card-subtitle>
      最後更新時間: {{ lastUpdate }}
    </v-card-subtitle>
    <v-divider
        :thickness="3"
        class="border-opacity-100"
        color="success"
    ></v-divider>
  </v-card>
  <div v-else>
  </div>

  <v-data-table
      v-if="jsonData"
      v-model:page="page"
      v-model:items-per-page="itemsPerPage"
      :headers="headers"
      :items="itemData"
      :item-key="item => item.UUID"
      item-value="systematics"
      class="elevation-1"
  >

    <template v-slot:item="{ item }">
      <tr>
        <td>{{ item.columns.systematics }}</td>
        <td><a :href="item.columns.source_url" target="_blank">{{ item.columns.title }}</a></td>
        <td>{{ item.columns.date }}</td>
        <td>{{ item.columns.address }}</td>
        <td>
          <v-img :src="item.columns.figure" :width="300"></v-img>
        </td>
      </tr>
    </template>
  </v-data-table>

  <div v-else class="text-center">
    <p>選擇上面的地點/展覽 來看看吧~</p>
  </div>

</template>

<script>
import {VDataTable} from 'vuetify/labs/VDataTable'

import {exhibitionTopicClassObject, URL_SOURCE_OBJECT} from '../assets/source.js'


export default {
  name: 'playTable',
  components: {
    'v-data-table': VDataTable,
  },
  data() {
    return {
      page: 1,
      loading: true,
      itemsPerPage: 5,
      headers: [
        {title: '分類', key: 'systematics'},
        {title: '名稱', key: 'title'},
        {title: '日期', key: 'date'},
        {title: '地點', key: 'address'},
        {title: '縮圖', key: 'figure'},
      ],
      itemData: null,
      jsonData: null,
      information: null,
      lastUpdate: null,
      source: exhibitionTopicClassObject,
    };
  },
  methods: {
    loadData(url) {
      fetch(`${URL_SOURCE_OBJECT}${url}.json`)
          .then(response => response.json())
          .then(data => {
            this.jsonData = data;
            this.itemData = this.jsonData.data;
            this.information = this.jsonData.information
            let date = this.$moment(this.jsonData.last_update).tz('Asia/Taipei')
            console.log(date)
            console.log(`${date.fromNow()} (${date.format("LLLL")})`)
            this.lastUpdate = `${date.fromNow()} (${date.format("LLLL")})`
          })
          .catch(error => {
            console.error(error);
          });
    }
  },
}
</script>

<style scoped>

</style>